@charset "utf-8";

.eff{ -moz-transition: all .5s; -webkit-transition: all .5s; transition: all .5s;}
.animated{-webkit-animation-duration: .3s; animation-duration: .3s; -webkit-animation-fill-mode: both; animation-fill-mode: both; }
.animated.infinite{-webkit-animation-iteration-count: infinite; animation-iteration-count: infinite;}
.animated.hinge{-webkit-animation-duration: 2s; animation-duration: 2s;}
@-webkit-keyframes bounce{0%, 20%, 50%, 80%, 100%{-webkit-transform: translateY(0); transform: translateY(0);} 40%{-webkit-transform: translateY(-30px); transform: translateY(-30px);} 60%{-webkit-transform: translateY(-15px); transform: translateY(-15px);}}
@keyframes bounce{0%, 20%, 50%, 80%, 100%{-webkit-transform: translateY(0); -ms-transform: translateY(0); transform: translateY(0);} 40%{-webkit-transform: translateY(-30px); -ms-transform: translateY(-30px); transform: translateY(-30px);} 60%{-webkit-transform: translateY(-15px); -ms-transform: translateY(-15px); transform: translateY(-15px);}}
.bounce{-webkit-animation-name: bounce; animation-name: bounce;}
@-webkit-keyframes flash{0%, 50%, 100%{opacity: 1;} 25%, 75%{opacity: 0;}}
@keyframes flash{0%, 50%, 100%{opacity: 1;} 25%, 75%{opacity: 0;}}
.bounceIn{-webkit-animation-name: bounceIn; animation-name: bounceIn;}
@-webkit-keyframes bounceInDown{0%{opacity: 0; -webkit-transform: translateY(-2000px); transform: translateY(-2000px);} 60%{opacity: 1; -webkit-transform: translateY(30px); transform: translateY(30px);} 80%{-webkit-transform: translateY(-10px); transform: translateY(-10px);} 100%{-webkit-transform: translateY(0); transform: translateY(0);}}
@keyframes bounceInDown{0%{opacity: 0; -webkit-transform: translateY(-2000px); -ms-transform: translateY(-2000px); transform: translateY(-2000px);} 60%{opacity: 1; -webkit-transform: translateY(30px); -ms-transform: translateY(30px); transform: translateY(30px);} 80%{-webkit-transform: translateY(-10px); -ms-transform: translateY(-10px); transform: translateY(-10px);} 100%{-webkit-transform: translateY(0); -ms-transform: translateY(0); transform: translateY(0);}}
.bounceInDown{-webkit-animation-name: bounceInDown; animation-name: bounceInDown;}
@-webkit-keyframes bounceInLeft{0%{opacity: 0; -webkit-transform: translateX(-2000px); transform: translateX(-2000px);} 60%{opacity: 1; -webkit-transform: translateX(30px); transform: translateX(30px);} 80%{-webkit-transform: translateX(-10px); transform: translateX(-10px);} 100%{-webkit-transform: translateX(0); transform: translateX(0);}}
@keyframes bounceInLeft{0%{opacity: 0; -webkit-transform: translateX(-2000px); -ms-transform: translateX(-2000px); transform: translateX(-2000px);} 60%{opacity: 1; -webkit-transform: translateX(30px); -ms-transform: translateX(30px); transform: translateX(30px);} 80%{-webkit-transform: translateX(-10px); -ms-transform: translateX(-10px); transform: translateX(-10px);} 100%{-webkit-transform: translateX(0); -ms-transform: translateX(0); transform: translateX(0);}}
.bounceInLeft{-webkit-animation-name: bounceInLeft; animation-name: bounceInLeft;}
@-webkit-keyframes bounceInRight{0%{opacity: 0; -webkit-transform: translateX(2000px); transform: translateX(2000px);} 60%{opacity: 1; -webkit-transform: translateX(-30px); transform: translateX(-30px);} 80%{-webkit-transform: translateX(10px); transform: translateX(10px);} 100%{-webkit-transform: translateX(0); transform: translateX(0);}}
@keyframes bounceInRight{0%{opacity: 0; -webkit-transform: translateX(2000px); -ms-transform: translateX(2000px); transform: translateX(2000px);} 60%{opacity: 1; -webkit-transform: translateX(-30px); -ms-transform: translateX(-30px); transform: translateX(-30px);} 80%{-webkit-transform: translateX(10px); -ms-transform: translateX(10px); transform: translateX(10px);} 100%{-webkit-transform: translateX(0); -ms-transform: translateX(0); transform: translateX(0);}}
.bounceInRight{-webkit-animation-name: bounceInRight; animation-name: bounceInRight;}
@-webkit-keyframes bounceInUp{0%{opacity: 0; -webkit-transform: translateY(1000px); transform: translateY(1000px);}100%{-webkit-transform: translateY(0); transform: translateY(0);}}
/*@keyframes bounceInUp{0%{opacity: 0; -webkit-transform: translateY(500px); transform: translateY(500px);} 100%{-webkit-transform: translateY(0); -ms-transform: translateY(0); transform: translateY(0);}}*/
.bounceInUp{-webkit-animation-name: bounceInUp; animation-name: bounceInUp;}
@-webkit-keyframes bounceOut{0%{-webkit-transform: scale(1); transform: scale(1);} 25%{-webkit-transform: scale(.95); transform: scale(.95);} 50%{opacity: 1; -webkit-transform: scale(1.1); transform: scale(1.1);} 100%{opacity: 0; -webkit-transform: scale(.3); transform: scale(.3);}}
@keyframes bounceOut{0%{-webkit-transform: scale(1); -ms-transform: scale(1); transform: scale(1);} 25%{-webkit-transform: scale(.95); -ms-transform: scale(.95); transform: scale(.95);} 50%{opacity: 1; -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transform: scale(1.1);} 100%{opacity: 0; -webkit-transform: scale(.3); -ms-transform: scale(.3); transform: scale(.3);}}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}



/** button **/
.f-ui_btn{ -webkit-box-sizing: border-box;  -moz-box-sizing: border-box;  -webkit-ms-sizing: border-box; display: inline-block; height: 34px; line-height: 34px; border-bottom:2px solid #9b9b9b; background-color: #c2c2c2;   font-size: 0.408rem; text-align: center; color: #fff; /*padding: 0 15px;*/ min-width: 90px; }

.f-ui_button{ -webkit-box-sizing: border-box;  -moz-box-sizing: border-box;  -webkit-ms-sizing: border-box; font-size: 14px; text-align: center; display: inline-block; background: #33a7fa; padding: 0.6em 15px; color: #fff; border-radius: 3px; text-decoration: none; border: 1px solid #d2d2de; background: #fff; color: #666 !important;}
.f-ui_button:active{ opacity: 0.8;}
.f-ui_button.block{ display: block; width: 100%;}
.f-ui_button.blue{ background: #33a7fa; border-color: #33a7fa; color: #fff !important; }
.f-ui_button.orange{  background:#f37b1d; border-color: #f37b1d; color: #fff !important;}
.f-ui_button.green{ background: #51bf87; border-color: #51bf87; color: #fff !important;}
.f-ui_button.red{ background: #ed533b; border-color: #ed533b; color: #fff !important;}
.f-ui_button.violet{ background: #c07ed8; border-color: #c07ed8; color: #fff !important;}
.f-ui_button.disable{  background:#d7d5d6; border-color: #d7d5d6; color: #999 !important;}



/** 遮罩**/
.f-ui_mask{ position:fixed; width: 100%; height: 100%; background: rgb(0,0,0); opacity: 0.8; left: 0; top: 0; z-index: 1000;}


/** checkbox **/
.f_ui-checkbox{ display: inline-block; width: 52px; height: 32px;  position: relative; vertical-align: middle; }
.f_ui-checkbox i{ position: absolute; width: 48px; height:26px; border: 1px solid rgba(224,223,223,.8); border-radius: 28px;  background: #fff;}
.f_ui-checkbox i:after{ content:''; position: absolute; width: 24px; height: 24px; border-radius: 50%; background: #fff; border:1px solid #d3d3d3; top: 0; left: -1px; -moz-transition: all .3s; -webkit-transition: all .3s; transition: all .3s;}
.f_ui-checkbox input{ display:none;}
.f_ui-checkbox input:checked~i{ background: #2eaef8; border-color: #85d5e9;}
.f_ui-checkbox input:checked~i:after{left: 22px;  border-color: #fff;}

/** message 提示 **/
.f_ui-msg{ font-size: 14px; /*max-width:250px; */overflow: hidden; line-height:36px; background: rgba(255,255,255,.9); border:1px solid #fff; border-radius: 3px; padding:0 12px; text-align: center;  box-shadow: 0 0 5px #666; position: fixed; /*left: 50%; top: 50%;*/ z-index: 9000;}
.f_ui-msg .msg_type{ position: relative; padding-left: 26px;}
/*.f_ui-msg .msg_type:before{ content: ''; position: absolute; left: 0; top: 50%; margin-top: -11px; width: 22px; height: 22px; background: url(../images/ui.png?v=9015) no-repeat;background-size: 100px auto;  color: #333;}*/
.f_ui-msg .type_1:before{ background-position:-54px -30px; }
.f_ui-msg .type_2:before{ background-position:-30px -30px; }
.f_ui-msg .type_3:before{ background-position:-80px -30px; }
/*.f_ui-msg .type_1{ color: #2fad64;}
.f_ui-msg .type_2{ color: #3bb4f2;}
.f_ui-msg .type_3{ color: #dc2640;}*/
.f_ui-msg .type_4{ color: #333; padding-left: 0;}
.f_ui-msg .type_4:before{ display: none; }



/** loadding **/
.f_ui-loading{ position:fixed;width: 100%; height: 100%;left: 0px; top: 0px; z-index: 9000;}
.f_ui-loading.load_mask{ position: fixed; left: 0; top: 0; width: 100%; height: 100%; background: rgba(0,0,0,.4);}
.f_ui-loading .load_body{ text-align: center; position: fixed; width:70px; padding: 18px 0 8px; /*height: 36px;*/ text-align: center; left:50%; top: 50%; margin-left: -35px; margin-top: -30px;  background: rgba(0,0,0,.6); border-radius: 10px; color: #fff; }
.f_ui-loading .load_text{ display: block; font-size: 12px; font-weight: normal; color: #ddd; line-height: 30px; }
@-moz-keyframes typing{0%{background-color: rgba(255,255,255, 1); box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 24px 0px 0px 0px rgba(255,255,255,0.2);} 25%{background-color: rgba(255,255,255, 0.4); box-shadow: 12px 0px 0px 0px rgba(255,255,255,2), 24px 0px 0px 0px rgba(255,255,255,0.2);} 75%{background-color: rgba(255,255,255, 0.4); box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 24px 0px 0px 0px rgba(255,255,255,1);}}
@keyframes typing{0%{background-color: rgba(255,255,255, 1); box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 24px 0px 0px 0px rgba(255,255,255,0.2);} 25%{background-color: rgba(255,255,255, 0.4); box-shadow: 12px 0px 0px 0px rgba(255,255,255,2), 24px 0px 0px 0px rgba(255,255,255,0.2);} 75%{background-color: rgba(255,255,255, 0.4); box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 24px 0px 0px 0px rgba(255,255,255,1);}}


/** 弹窗 **/
.f-ui_dialog{  background: #fff;  overflow: hidden; border-radius: 0.24rem; }
.f-ui_dialog .hd{ height:1.2rem; line-height:1.2rem; color: #ff6000;  /*padding-left:10px;*/ border-bottom: 1px solid #dadfe2; font-size: 0.36rem; position: relative; background: #f8f8f9; }
.f-ui_dialog.dialog_bottom{ border-radius: 0;}
.f-ui_dialog.dialog_bottom .hd{  height:1.2rem; line-height:1.2rem; color: #1081b9; border-bottom-color:#d7d7d7; background: #fff;  }
.f-ui_dialog .hd .title{ height: 100%;overflow: hidden; text-overflow:ellipsis; text-align: center;  white-space: nowrap;/* padding-right: 40px;*/ }
.f-ui_dialog .hd .f-ui_btn.c{ right:0; background-position:15px 14px; }
.f-ui_dialog .hd .f-ui_btn.o{ left:0; background-position: -24px 12px;  }
/*.f-ui_dialog .hd .close,.f-ui_confirm .title:after{ background: url(../images/ui.png?v=95) no-repeat; background-size: 100px auto;}*/
.f-ui_dialog .hd .close{ position: absolute; right: 8px; top:6px; width:30px; height: 30px; background-position: -72px 2px; }

.f-ui_dialog .bd {background: #fff;}
.f-ui_dialog .bd .c_bd{ padding:10px 15px; overflow-y:auto; -webkit-box-sizing: border-box;-moz-box-sizing: border-box;-ms-box-sizing: border-box; box-sizing: border-box; }
.f-ui_dialog .bd .c_ft .f-ui_btn{ width: 50%; border-radius: 0; height: 1.2rem; line-height: 1.2rem; border:none; background:#fff; color: #009af0; }
.f-ui_dialog .bd .c_ft .f-ui_btn:nth-child(2){ border-left: 1px solid #ddd;}
.f-ui_dialog .bd .c_ft .f-ui_btn.o{}
.f-ui_dialog .bd .c_ft{text-align: center; border-top: 1px solid #d7d7d7; overflow:hidden; text-align: right;}
.dialog_green .f-ui_btn.c { float: right;}
.dialog_green .f-ui_btn.o { float: left;  border-left: none!important;   border-right: 1px solid #ddd!important;color: #a4a4ac!important;}
.f-ui_confirm .hd .title{ position: relative; padding-left: 30px; margin-left: 10px; text-align: left;}
.f-ui_confirm .hd .title:after{ position: absolute; content: ''; width: 22px; height: 22px; background-position: 0 -32px; left: 0; top: 50%; margin-top: -10px;}
.f-ui_confirm .hd{ text-align: left;}
.f-ui_confirm .bd .c_bd{ text-align: center; font-size: 16px; width: 100%; display: table;}
.f-ui_confirm .bd .c_bd .confirm_text{ display: table-cell; vertical-align: middle; padding: 15px 0; }
.f-ui_confirm .bd .c_ft{padding: 12px; text-align:center;}
.f-ui_confirm .bd .c_ft .f-ui_btn{ margin: 0 10px; width: auto; height: 34px; line-height: 34px; border-radius: 5px; color: #fff; border-bottom:2px solid;}
.f-ui_confirm .bd .c_ft .o{ background: #2eaef9; border-bottom-color: #248bc6;}
.f-ui_confirm .bd .c_ft .c{ background: #c2c2c2; border-bottom-color: #9b9b9b; }
.f-ui_confirm .bd .c_ft .f-ui_btn.o{ color:#fff;}

/** listPicker **/
.f-ui_listpicker {height: 170px;  background: #fff;  overflow: hidden; position: relative; z-index: 1;}
.f-ui_listpicker:after{ content: ''; width: 100%; height: 32px; line-height: 32px; border-top: 1px solid #d7d7d7; border-bottom: 1px solid #d7d7d7;  position: absolute; left:0;  top: 50%; margin-top: -17px; background: #45b3ee; z-index: -1;}
.f-ui_listpicker ul,
.f-ui_listpicker ul li{ list-style: none;}
.f-ui_listpicker ul li{ height: 34px; line-height: 34px; overflow: hidden; text-align: center; color: #565656; }
.f-ui_listpicker ul li.on{ color: #fff;}

.f-ui_listpicker::before,.f-ui_listpicker>i{ content: ''; position: absolute; left: 0; width: 100%; height: 68px;z-index: 5;}

.f-ui_listpicker::before{ bottom: 0; background:-webkit-linear-gradient(top, rgba(255,255,255,0.1),rgba(255,255,255,.8)); }
.f-ui_listpicker>i{ top: 0; background:-webkit-linear-gradient(top,rgba(255,255,255,.8),rgba(255,255,255,0.1)); }


/** 表单验证 **/
/* .validform_error{ background: #ffe7e7 !important; border:1px solid #e5a3a3 !important; } */
.twinkling{ -webkit-animation : twinkling .3s 3 ease-in-out; }
@-webkit-keyframes twinkling{0%{opacity:0.2;} 100%{opacity:1;}}
@-moz-keyframes twinkling{0%{opacity:0.2;} 100%{opacity:1;}}

/** 轮播图 **/
.f-ui_slider{ width: 100%; max-width: 1200px; min-height: 100px; margin:0 auto; overflow: hidden; position:relative;}
.f-ui_slider .list {  width: 100%; }
.f-ui_slider .list li{ float: left;}
.f-ui_slider .list li img{ width: 100%;  min-height: 100px;}
.f-ui_slider .list li{ display: none;}
.f-ui_slider .dots{ position: absolute; height: 10px; line-height:10px; width: 100%; bottom: 12px; left: 0; text-align: center; z-index: 10; }
.f-ui_slider .dots li{ display: inline-block; width: 8px; height: 8px; background: #999; border: 1px solid #999; border-radius: 50%; margin: 0 3px;}
.f-ui_slider .dots li.active{ background: #17a0e8; border-color: #17a0e8;}






