/*滑动2*/
.noUi-base {
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%
}

.noUi-target {
  position: relative;
  width: 100%;
  height: 0.5rem;
}

.noUi-target:after {
  content: "";
  display: block;
  position: absolute;
  left: -50%;
  width: 200%;
  top: 0;
  height: 1px;
  background: #d8d8d8;
  -webkit-transform: scale(0.5);
}

.noUi-connect {
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  background: #FF6000;
  height: 0.096rem;
}

.noUi-origin {
  position: absolute;
  height: 0;
  width: 0
}

.noUi-handle {
  position: relative;
  z-index: 1
}

.noUi-state-tap .noUi-connect, .noUi-state-tap .noUi-origin {
  -webkit-transition: top .3s, right .3s, bottom .3s, left .3s;
  transition: top .3s, right .3s, bottom .3s, left .3s
}

.noUi-horizontal .noUi-handle {
  width: 0.8rem;
  height: 0.8rem;
  left: -18px;
  top: -0.4rem;
  border-radius: 100%;
  background-color: #f8f8f9;
}

.noUi-handle {
  border: 1px solid #FF6000;
  border-radius: 3px;
  background: #FFF;
  cursor: default;
}

.noUi-active {
  box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #DDD, 0 3px 6px -3px #BBB
}

.noUi-pips, .noUi-pips * {
  -moz-box-sizing: border-box;
  box-sizing: border-box
}

.noUi-pips {
  position: absolute;
  color: #999
}

.noUi-value {
  position: absolute;
  text-align: center;
  top: -0.65rem;
  font-size: 0.312rem;
  color: #ff6000;
  margin-left: -0.5rem;
  width: 1rem;
}

.noUi-value em {
  font-size: 0.212rem;
}

.noUi-value-sub {
  color: #ccc;
  font-size: 10px
}

.noUi-marker {
  position: absolute;
  background: #CCC
}

.noUi-marker-large, .noUi-marker-sub {
  background: #d8d8d8
}

.noUi-pips-horizontal {
  left: 0;
  width: 100%;
  position: absolute;
  top: -0.5rem;
}

.noUi-marker-horizontal.noUi-marker {
  margin-left: -1px;
  width: 1px;
  height: 5px
}

.noUi-marker-horizontal.noUi-marker-normal {
  display: none;
}

.noUi-marker-horizontal.noUi-marker-large {
  height: 0.26rem;
  position: absolute;
  top: 0.25rem
}

.noUi-state-tap .noUi-connect, .noUi-state-tap .noUi-origin {
  -webkit-transition: top .3s, right .3s, bottom .3s, left .3s;
  transition: top .3s, right .3s, bottom .3s, left .3s
}

