  -moz-animation: load 1s linear infinite alternate;
  animation: load 1s linear infinite alternate;
}

@-webkit-keyframes load {
  0% {
    background-color: #F7B2AE;
    box-shadow: 12px 0px 0px 0px  #FBD1CE, 24px 0px 0px 0px #FBD1CE;
  }
  25% {
    background-color: #FBD1CE;
    box-shadow: 12px 0px 0px 0px #FEE8E7, 24px 0px 0px 0px #FEE8E7;
  }
  75% {
    background-color: #FEE8E7;
    box-shadow: 12px 0px 0px 0px #F7B2AE, 24px 0px 0px 0px #F7B2AE;
  }
}
