  opacity:0 !important;
  animation:notice_animate2 .6s !important;
}

@keyframes notice_animate2{
  0%{
    opacity: 1;
    transform: translate(-50% ,0);
  }
  100%{
    opacity: 0;
    transform: translate(-50%,40px);
  }
}

@keyframes notice_animate{
  0%{
    opacity: 0;
    transform: translate(-50%,40px);
  }
  100%{
    opacity: 1;
    transform: translate(-50% ,0);
  }
}


